.founders {
  margin-bottom: 100px;
  &__wrapper {
    padding-top: 50px;
  }

  &__slide {
    min-height: 200px;
    background-color: var(--light-color);
    padding: 130px var(--spacing-extra-large) 50px;
    text-align: center;
    border-radius: 15px;
    box-shadow: var(--box-shadow);
    position: relative;
  }

  &__img {
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50%;
  }

  &__name {
    font-size: 31px;
  }

  &__descr {
  }

  &__adress {
  }
}
